import React, { Component } from 'react';
import { blogList } from '../helpers/blogList';
import BlogItem from '../components/blogItem';
import '../styles/blogPosts.css';
import blogItem from '../components/blogItem';


export default class blogPosts extends Component {

    render() {

        
     function Open() {
         if(this.props.match.params.id){
            alert('I work or whatever');
         }
        
    }
        return (
            <div className="blog">
                <h1 className="blogTitle">Blog Posts</h1>
                <div className="blogList">
                    {blogList.map((blogItem, value) =>{
                        return(
                           <button className="bloglinks" onClick={Open}>
                               <BlogItem key={blogItem.id} value={value} name={blogItem.name} image={blogItem.image} text={blogItem.text}/>
                               </button> 
                           
                        )
                    })}
                   
                </div>
                
            </div>
        )
    }
}

